
.scoreboard {
    display: grid;
    gap: 20px;
    width: 100%;
  }

  .row {
    margin-left: 160px;
    margin-right: 160px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr; /* Ensures 4 equal columns for each row */
    gap: 40px;
    align-items: center;
  }

  .header-item {
    font-weight: bold;
    text-align: center;
    padding-bottom: 10px;
    border-bottom: 2px solid white;
  }

.team, .score {
    font-size: 20px;
    text-align: center;
  }

  .user {
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
  }

  .timePlayed {
    font-size: 20px;
  }

  .country-flag {
    width: 30px;
    height: 30px;
    background-size: cover;
    background-position: center;
  }