.App {
  text-align: center;
  height: 100%;
  justify-content: center;
  align-items: center;
  background-image: url(LeaderboardsBckg.png);
  background-size: cover;
  background-repeat: no-repeat;
}

.App-logo {
  justify-content: flex-start;
  width: 160px;
  height: 80px;
}

.App-header {
  background-color: #161616;
  height: 160px;
  width: 100%;
  display: flex;
  gap: 40px;
  flex-direction: row;
  align-items: center;
  justify-content: left;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-left: 40px;
}

.Leaderboard {
  background-color: #282c34b2;
  margin-left: 100px;
  margin-right: 100px;
  padding-top: 40px;
  padding-bottom: 80px;
  justify-content: center;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@font-face {
  font-family: 'Audiowide';
  src: url('fonts/Audiowide-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

.nav-menu {
  list-style-type: none;  /* Removes bullet points */
  display: flex;          /* Makes the menu horizontal */
  gap: 20px;              /* Adds space between items */
  padding: 0;             /* Removes default padding */
  margin: 0;              /* Removes default margin */
  justify-content: center;
}

.nav-menu li a {
  font-family: 'Audiowide', sans-serif;
  text-decoration: none;
  color: #ffffff;                       /* White text color */
  font-weight: bold;
  font-size: 16px;
  padding: 10px 20px;
  border-radius: 8px;                   /* Rounded corners for tab look */
  background: linear-gradient(145deg, #222831, #393e46);  /* Dark, futuristic gradient */
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.4),             /* Shadow for depth */
              inset 0px 0px 6px rgba(255, 255, 255, 0.1); /* Inner shadow for 3D effect */
  transition: all 0.3s ease;            /* Smooth transition on hover */
}

/* Ensure that hover effects for normal buttons don't affect green buttons */
.nav-menu li a:hover {
  color: #3ce5ff;                       /* Neon hover color */
  background: linear-gradient(145deg, #393e46, #222831);  /* Gradient reverse */
  box-shadow: 0px 4px 15px rgba(0, 204, 255, 0.6),        /* Outer neon glow */
              inset 0px 0px 10px rgba(0, 204, 255, 0.3);  /* Inner glow */
  transform: scale(1.05);             
}

.nav-menu li a.green-button {
  background: rgba(0, 128, 0, 0.719) !important; /* Use background property to override */
  color: white;
}

.nav-menu li a.green-button:hover {
  background: darkgreen;
}

.nav-menu li a.yellow-button {
  background: rgba(126, 128, 0, 0.719) !important; /* Use background property to override */
  color: white;
}

.nav-menu li a.yellow-button:hover {
  background: rgb(100, 87, 0);
}
